.redirect-player {
  background-color: var(--secondary-color);
  position: fixed;
  top: 50%;
  left: 50%;
  width: 0%;
  height: 0%;
  z-index: 98;
  cursor: default;
  transform: translate(-50%, -50%);
  transition: width var(--animation-time), height var(--animation-time);
  clip-path: circle(20%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999;
}

.redirect-player.play {
  width: 600%;
  height: 900%;
  top: 50%;
  left: 50%;
}

.redirect-player > img {
  width: 124px;
  height: 124px;
  clip-path: circle(0);
  transition: clip-path var(--animation-time);
}

.redirect-player.play > img {
  clip-path: circle(100%);
}