html, body, #root {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: var(--primary-color);
  cursor: var(--cursor-type);
}

.container {
  width: 100%;
  height: 100vh;
  max-width: 1536px;
  margin: auto;
}

@font-face {
  font-family: "archyedt";
  src: url("../public/archyedt-bold.otf");
}

* {
  box-sizing: border-box;
  font-family: archyedt;
}

.title-header {
  width: 100%;
  border-bottom: 1px solid var(--green-color);
  display: flex;
  padding: 0;
}

.title-header.right {
  justify-content: end;
}

.title-header > p {
  display: inline-block;
  background-color: var(--green-color);
  color: white;
  padding: 20px 50px;
  margin: 0;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
  font-size: 36px;
}

:root {
  --primary-color: rgb(33, 33, 33);
  --secondary-color: rgb(66, 66, 66);
  --green-color: rgb(40 126 41);
  --cursor-type: "none"
}