.section-header {
    display: flex;
    flex-direction: row;
    height: 100vh;
    width: 100%;
}

.main-banner {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 100%;
    align-items: center;
}

.main-banner-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
}


.main-banner-content > p {
    color: white;
    padding: 0;
    margin: 0;
    font-size: 62px;
    font-weight: lighter;
    text-align: center;
}

.main-banner > img {
    width: 500px;
    height: fit-content;
}

.header-services {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    width: 100%;
    height: 500px;
}

.header-services > .service {
    height: 100%;
    background-color: gray;
    border-radius: 16px;
    margin: 20px;
    overflow: hidden;
}

.header-services > .service > img {
    width: 100%;
    height: 100%;
    transition: 0.5s;
}

.header-services > .service > img:hover {
    transform: scale(1.1);
}