.circle {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    position: fixed;
    transition: height 0.2s, width 0.2s;
    z-index: 999;
    pointer-events: none;
    cursor: none;
    overflow: hidden;
    margin: 10px;
    background: var(--green-color);
}

.circle.active {
    width: 256px;
    height: 256px;
}


.circle>pre {
    position: relative;
    padding: 0;
    margin: 0;
    width: fit-content;
    height: fit-content;
}