.header {
    position: fixed!important;
    width: 100%!important;
    top: 0;
    z-index: 99;
    background-color: var(--secondary-color)!important;
}

.navbar-items-left {
    width: 100%;
}

.navbar-item-left {
    transition: 0.5s;
    color: white!important;
}

.navbar-item-left:hover {
    cursor: pointer;
}

.navbar-item-left.top {
    color: white;
    text-decoration: none;
    padding: 15px 30px;
    margin: 0 5px;
    transition: 0.5s;
}

.navbar-item-left.top:hover, .navbar-item-left:hover {
    background-color: var(--green-color)!important;
}

.navbar-pages-list {
    justify-content: flex-end;
}